import React from "react";
import styled from "styled-components";

export const LogoComponent = () => {
  const Logo = styled.div`
    position: relative;
    width: 100px;
    margin-left: 5px;

    span {
      position: absolute;
      top: 1px;
      font-weight: 700;
      font-size: small;
    }
    img {
      margin-top: 5px;
    }
  `;
  return (
    <Logo>
      <img src="/images/logo.png" />
      <span>WHAT TO WHATCH</span>
    </Logo>
  );
};
